.articleContainer{
    max-width: 800px;
    margin: auto;
    padding: 40px;
    
}

.flex{
    display: flex;
}

.graphical{
    
}

.green1{
    background-color: rgba(150, 255, 200, 1);
}

.green2{
    background-color: rgba(170, 255, 220, 1);
}

.blue1{
    background-color: rgba(150, 220, 255, 1);
}
.yellow1{
    background-color: rgba(220, 220, 155, 1);
}

.containerLevel0, .containerLevel1, .containerLevel2{
    padding: 1rem;
}
.containerLevel0{
    font-size: 1.5rem;
}
.containerLevel1{
    font-size: 1.25rem;
}
.containerLevel2{
    font-size: .9rem;
}

.twoCols{
    display: flex;
    justify-content: space-between;
}

.horizontalFitHalf{
    width: calc(50% - 2.5rem);
}

.paddingBottomX{
    padding-bottom: 150px;
}


